.footerbody{
    height: 350px;
    background-color: rgb(31, 31, 31);
}

.footermesection{
    color: white;
}
.footername{
    background: #AD5FCF;
background: linear-gradient(to right, rgba(209,0,116,1) 10%, rgba(110,44,107,1)  100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}

.footerdivider{
    height: 1%;
    width: 40%;
    background: linear-gradient(to right, rgba(209,0,116,1) 10%, rgba(110,44,107,1)  100%);
}

.bio{
    font-size: 15px;
    font-weight: 200;
    font-style: italic;
}

#footersocialicons{
    background: linear-gradient(to right, rgba(209,0,116,1) 10%, rgba(110,44,107,1)  100%);
    color: white;
    padding-bottom: 10px;
}

#footersocialicons1{
    background: linear-gradient(to right, rgba(209,0,116,1) 10%, rgba(110,44,107,1)  100%);
    color: white;
    padding-bottom: 10px;
    padding-bottom: 9px;
    padding-right: 7px;
    padding-left: 11px;
}

#footersocialicons2{
    background: linear-gradient(to right, rgba(209,0,116,1) 10%, rgba(110,44,107,1)  100%);
    color: white;
    padding-bottom: 10px;
    padding-bottom: 9px;
    padding-right: 7px;
    padding-left: 11px;
}

.footergithublink{
    color: white !important;
    text-decoration: none;
    transition-duration: 0.5s;
    cursor: pointer;
}

.footergithublink:hover{
    color: rgba(209,0,116,1) !important;
    text-decoration: none;
}



.site-footer
{
  background-color:#26272b;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#737373;
}
.site-footer hr
{
  border-top-color:#bbb;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#737373;
}
.site-footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none
}
.footer-links li
{
  display:block
}
.footer-links a
{
  color:#8e8e8e;
  text-decoration: none;
  transition-duration: 0.5;
  
}



.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
    color: rgba(209,0,116,1) ;
    text-decoration: none; 
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#33353d
}
.copyright-text
{
  margin:0
}
@media (max-width:991px)
{
  .site-footer [class^=col-]
  {
    margin-bottom:30px
  }
}



@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:#96a2b2;
  font-weight:700;
  font-size:13px
}
.social-icons a{
  background-color:#eceeef;
  color:#818a91;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  -webkit-transition:all .2s linear;
  -o-transition:all .2s linear;
  transition:all .2s linear
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color:#29aafe
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.twitter:hover
{
  background-color:#00aced
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}
.social-icons a.dribbble:hover
{
  background-color:#ea4c89
}





@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
}



@media (max-width:660px)
{

  #footersections{
    padding-right: 5%;
    text-align: center;
  }
  .footergitlink{
   margin-left: 26%;
   margin-bottom: 4%;
  }

}
